import Head from 'next/head'

class Meta extends React.Component {
  constructor(props) {
    super(props)

    if (!this.props.title) {
      this.props.title = 'Interactions Lab | University of Calgary HCI Group'
    }
    if (!this.props.description) {
      this.props.title = 'Human-Computer Interaction and Information Visualization Group at the University of Calgary'
    }



    this.getPhotos()
  }


  render() {
    return (
      <Head>
        <meta name="description" content="Human-Computer Interaction and Information Visualization Group at the University of Calgary" />

        <meta property="og:title" content="Interactions Lab | University of Calgary HCI Group" />
        <meta property="og:site_name" content="Interactions Lab | University of Calgary HCI Group" />
        <meta property="og:description" content="Human-Computer Interaction and Information Visualization Group at the University of Calgary" />
        <meta property="og:type" content="website" />
        <meta property="og:url" content="https://ilab.ucalgary.ca/" />
        <meta property="og:image" content="/static/images/cover.jpg" />
        <meta name="twitter:card" content="summary" />
        <meta name="twitter:site" content="https://ilab.ucalgary.ca/" />
        <meta name="twitter:image" content="/static/images/cover.jpg" />
      </Head>
    )
  }
}

export default Meta